//===========================================
// IMPORT DEPENDENCIES
//===========================================
import React, { Component } from "react";
import {
    Button,
    Card,
    CardBody,
    CardHeader,
    Col,
    Form,
    FormGroup,
    Container,
    Input,
    Label,
    Row,
  } from 'reactstrap';
  import axios from 'axios';
import './assets/css/main.css';
import './custom.css';
import pic01 from "./images/pic01.jpg"
import pic02 from "./images/pic02.jpg"
import pic03 from "./images/pic03.jpg"
import Slideshow from "./Slideshow";
import slide1 from "./assets/slide1.jpg";
import slide2 from "./assets/slide2.jpg";
import slide3 from "./assets/slide3.jpg";
import slide4 from "./assets/slide4.jpg";
import slide5 from "./assets/slide5.jpg";

//===========================================
// CREATE STYLES OBJECT
//===========================================
const s = {
    container: "screenW screenH dGray cols",
    header: "flex1 fCenter fSize2",
    main: "flex8 white",
    footer: "flex1 fCenter"
};

//===========================================
// SLIDES DATA
//===========================================
const slides = [slide1, slide2, slide3, slide4, slide5];

//===========================================
// APP COMPONENT
//===========================================
class Homepage extends Component {
    constructor() {
        super()
        this.state={
            userName:'',
            email:'',
            subject:'',
            message:'',
            contact:'',
            emailborderColor:'none',
            contactborderColor:'none',
            isValidated:true
        }
        this.onChange = this.onChange.bind(this);
    }
    onChange(e) {
        this.setState({ [e.target.name]: e.target.value })
      }

    sendEmail=async()=>{
        this.checkEmail();
        if(this.state.isValidated){
        await axios.post('/send-email',{
            userName : this.state.userName,
            email: this.state.email,
            subject : this.state.subject,
            message : this.state.message,
            contact : this.state.contact
        }).then(res=>{
            var status=res.data.status;
            if(!status.localeCompare('Sent')){
              alert('Your Message Sent');
              this.clearForm();
            }
            else{
              alert('Please enter a valid email')
            }
        })
    }
    }
    checkEmail=()=>{

        if (!/^[a-zA-Z0-9]+@[a-zA-Z0-9]+\.[A-Za-z]+$/.test(this.state.email)) {

            this.setState({
                emailborderColor:'red',
                isValidated:false
            })
        }
        else{
            this.setState({
                emailborderColor:'blue',
                isValidated:true
            })

        }
        if(!/\d[9]/.test(this.state.contact)){
            console.log("false")
            this.setState({
                contactborderColor:'red',
                isValidated:false
            })

        }
        else{
            this.setState({
                contactborderColor:'blue',
                isValidated:true
            })

        }
    }
    clearForm=()=>{
        this.setState({
            userName:'',
            email:'',
            subject:'',
            message:'',
            contact:'',
        })
    }
    render() {
        return (
            
            <div>
                
            
                   
                        <header id="header" class="alt">

                            <div class="logo"><a> Reliable Group Sri Lanka</a></div>
                            
                            <div class="toggle">
                                
                            <Button onClick={()=>this.props.history.push('/login')} class="dropbtn" style={{backgroundColor:'#aaff80'}}>Sign In</Button>

                            </div> 

                        </header>
            

            
                    
                  
                        <section  class="wrapper style3">
                        <div className={s.container}>
                <div className={s.main}>
                    <Slideshow slides={slides} />
                </div>
                <div className={s.footer}>Soil Transporting</div>
            </div>
            </section>     
                    
                        <section id="one" class="wrapper style3">
                            <div class="inner">
                                <div class="div">
                                    <div class="box">
                                        <div class="image fit">
                                            <img src={pic01} alt="" />
                                        </div>
                                        <div class="content">
                                            <header class="align-center">
                                                <h2 class="h2">Reliable Grop Sri Lanka(Pvt LTD)</h2>
                                                <p class="p">A soil transporting company</p>
                                            </header>
                                            <hr />
                                            <p class="p">Reliable Group Sri lanka is a company that transport soils for road developmet areas, Construction sites. We are currently working with many companies and we pprovide their soil needs at reasonable rate. We are looking forward to give be great service for our customers</p>
                                            
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </section>
            
                   
                        <section id="two" class="wrapper style3">
                            <div class="inner">
                                <div id="flexgrid">
                                    <div class="div">
                                        <header>
                                            <h3 class="h3">About Us</h3>
                                        </header>
                                        <p class="p">We are a well known company for transporting soils all over the district</p>
                                        <ul class="actions">
                                            <li><a href="#" class="button alt">Learn More</a></li>
                                        </ul>
                                    </div>
                                    <div>
                                        <header>
                                            <h3 class="h3">Contact Us</h3>
                                        </header>
                                        <p class="p">N0.524, Lot 7, Hadungamuwa, Matale. Tel:0765 505 252/ 0717 615 911 ෧බාරළු, වැලි,ගල් සැපයුම්කරු</p>
                                        <ul class="actions">
                                            <li><a href="#" class="button alt">Learn More</a></li>
                                        </ul>
                                    </div>
                                    <div>
                                        <header>
                                            <h3 class="h3">Our Mission</h3>
                                        </header>
                                        <p class="p">Our mission is to provide a good service to our customers</p>
                                        <ul class="actions">
                                            <li><a href="#" class="button alt">Learn More</a></li>
                                        </ul>
                                    </div>
                                </div>
                            </div>
                        </section>
            
                  
                        <section id="three" class="wrapper style3">
                            <div class="inner">
                                <div class="grid-style">
            
                                    <div class="div">
                                        <div class="box">
                                            <div class="image fit">
                                                <img src={pic02} alt="" />
                                            </div>
                                            <div class="content">
                                                <header class="align-center">
                                                    <h2 class ="h2">Reasonable Rates</h2>
                                                    <p class="p">Reasonable Rates for delivieries</p>
                                                </header>
                                                <hr />
                                                <p class="p"> We provide a good servece for a reasonable rates. Rate varies for the distance</p>
                                            </div>
                                        </div>
                                    </div>
            
                                    <div>
                                        <div class="box">
                                            <div class="image fit">
                                                <img src={pic03} alt="" />
                                            </div>
                                            <div class="content">
                                                <header class="align-center">
                                                    <h2 class="h2">Immediate Service</h2>
                                                    <p class="p">24/7 Service</p>
                                                </header>
                                                <hr />
                                                <p class="p"> Contact us for deliveries and we deliver soils as soon as possible</p>
                                            </div>
                                        </div>
                                    </div>
            
                                </div>
                            </div>
                        </section>
            
                  
                        <section id="four" class="wrapper style3">
                            <div class="inner">
            
                                <header class="align-center">
                                    <h2 class="h2">Immediate Service</h2>
                                    <p class="p" >Contact us for deliveries and we deliver soils as soon as possible</p>
                                </header>
            
                            </div>
                        </section>
            
                    
                        <footer id="footer" class="wrapper">
                            <div class="inner">
                                <section>
                                    <div class="box">
                                        <div class="content">
                                            <Card style={{backgroundColor:'#c2d6d6'}}>
                                                <CardHeader >
                                                    <h2 style={{textAlign:'center'}}>Contact Us</h2>
                                                </CardHeader>
                                                <CardBody>
                                                    <FormGroup row>
                                                        <Col>
                                                        <Label>Your Name :</Label>
                                                        <Input type="text" name="userName" value={this.state.userName} onChange={this.onChange}></Input>
                                                        </Col>
                                                        <Col>
                                                        <Label>Email :</Label>
                                                        <Input type="email" name="email" autoComplete="email" style={{borderBlockColor:this.state.emailborderColor}} value={this.state.email} onChange={this.onChange}></Input>
                                                        </Col>
                                                    </FormGroup>
                                                    <FormGroup>
                                                        <Label>Contact Number</Label>
                                                        <Input type="text" name="contact" style={{borderBlockColor:this.state.contactborderColor}} value={this.state.contact} onChange={this.onChange}></Input>
                                                    </FormGroup>
                                                    <FormGroup>
                                                        <Label>Topic</Label>
                                                        <Input type="textarea" name="subject" value={this.state.subject} onChange={this.onChange}></Input>
                                                    </FormGroup>
                                                    <FormGroup>
                                                        <Label>Your Message</Label>
                                                        <Input type="textarea" name="message" value={this.state.message} onChange={this.onChange}></Input>
                                                    </FormGroup>
                                                    <FormGroup row>
                                                        <Col md="8">
                                                        </Col>
                                                        <Col>
                                                        <Button  style={{width:'300px',backgroundColor:'#8080ff'}} onClick={()=>{this.sendEmail()}}>Send</Button>
                                                        </Col>
                                                    </FormGroup>
                                                </CardBody>
                                            </Card>
                                        </div>
                                    </div>
                                </section>
                                <div class="copyright">
                                    &copy; Reliable Group: <a href="#">Web Site</a>@2020.
                                </div>
                            </div>
                        </footer>

                
            </div>
        );
    }
}

export default Homepage;
